import { useNavigate } from "react-router-dom";
import BorderBottom from "../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/Refund/AdmisisonRefund.module.css";
import { useTranslation } from "react-i18next";
import FormToggle from "../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import { useEffect, useState } from "react";
import {
  useFinanceDashboardMaster,
  useFinanceTabController,
} from "../../../optimized/Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { Notfication } from "../../../validation/Snackbar";
const FinanceTabController = ({ financeId }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [state, setState] = useState({
    all_students: true,
    bank_details: true,
    data_export: true,
    deposits: true,
    exemption: true,
    expenses: true,
    fee_statistics: true,
    finance_mods: true,
    incomes: true,
    pending_fees: true,
    scholarships: true,
    submit_request: true,
    transaction_history: true,
  });
  const [run, setRun] = useState(false);
  const [msg, setMsg] = useState("");
  const [financeTabController] = useFinanceTabController();
  const { financeDashboardMaster } = useFinanceDashboardMaster({
    data: {
      fid: financeId,
      mid: "",
    },
    skip: !financeId,
  });

  useEffect(() => {
    if (financeDashboardMaster?.finance?.tab_manage) {
      setState({
        all_students: financeDashboardMaster?.finance?.tab_manage?.all_students,
        bank_details: financeDashboardMaster?.finance?.tab_manage?.bank_details,
        data_export: financeDashboardMaster?.finance?.tab_manage?.data_export,
        deposits: financeDashboardMaster?.finance?.tab_manage?.deposits,
        exemption: financeDashboardMaster?.finance?.tab_manage?.exemption,
        expenses: financeDashboardMaster?.finance?.tab_manage?.expenses,
        fee_statistics:
          financeDashboardMaster?.finance?.tab_manage?.fee_statistics,
        finance_mods: financeDashboardMaster?.finance?.tab_manage?.finance_mods,
        incomes: financeDashboardMaster?.finance?.tab_manage?.incomes,
        pending_fees: financeDashboardMaster?.finance?.tab_manage?.pending_fees,
        scholarships: financeDashboardMaster?.finance?.tab_manage?.scholarships,
        submit_request:
          financeDashboardMaster?.finance?.tab_manage?.submit_request,
        transaction_history:
          financeDashboardMaster?.finance?.tab_manage?.transaction_history,
      });
    }
  }, [financeDashboardMaster?.finance?.tab_manage]);
  const onBackWithArrowLeft = () => {
    navigate(-1);
  };

  const onSubmitFormHandler = (field, value) => {
    const updatedObj = { ...state };
    for (let obj in updatedObj) {
      if (obj === field) {
        updatedObj[obj] = value;
      }
    }
    financeTabController({
      fid: financeId,
      tabManage: { tab_manage: updatedObj },
    })
      .then(() => {
        setMsg("update tab access");
        setRun(true);
      })
      .catch({});
    setState((prev) => ({
      ...prev,
      [field]: value,
    }));
  };

  return (
    <>
      <div className={style.refund_container}>
        <section className={style.refund_container_title}>
          <img
            src="/images/arrow-left-fees-icon.svg"
            onClick={onBackWithArrowLeft}
            alt="go to previous tab"
            title="Go Back"
            style={{
              cursor: "pointer",
            }}
          />
          <div className={style.refund_container_title}>
            <h6>{t("tab_controller")}</h6>
          </div>
        </section>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("all_students")}
            checkedToggle={state.all_students}
            onToggleHandler={(e) =>
              onSubmitFormHandler("all_students", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("bank_details")}
            checkedToggle={state.bank_details}
            onToggleHandler={(e) =>
              onSubmitFormHandler("bank_details", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("data_export")}
            checkedToggle={state.data_export}
            onToggleHandler={(e) =>
              onSubmitFormHandler("data_export", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("deposits")}
            checkedToggle={state.deposits}
            onToggleHandler={(e) =>
              onSubmitFormHandler("deposits", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("exemption")}
            checkedToggle={state.exemption}
            onToggleHandler={(e) =>
              onSubmitFormHandler("exemption", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />

          <FormToggle
            labelHeading={t("expenses")}
            checkedToggle={state.expenses}
            onToggleHandler={(e) =>
              onSubmitFormHandler("expenses", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />

        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("fee_statistics")}
            checkedToggle={state.fee_statistics}
            onToggleHandler={(e) =>
              onSubmitFormHandler("fee_statistics", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />

          <FormToggle
            labelHeading={t("finance_mods")}
            checkedToggle={state.finance_mods}
            onToggleHandler={(e) =>
              onSubmitFormHandler("finance_mods", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("incomes")}
            checkedToggle={state.incomes}
            onToggleHandler={(e) =>
              onSubmitFormHandler("incomes", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />

        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("pending_fees")}
            checkedToggle={state.pending_fees}
            onToggleHandler={(e) =>
              onSubmitFormHandler("pending_fees", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
          <FormToggle
            labelHeading={t("scholarships")}
            checkedToggle={state.scholarships}
            onToggleHandler={(e) =>
              onSubmitFormHandler("scholarships", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />

          <FormToggle
            labelHeading={t("submit_request")}
            checkedToggle={state.submit_request}
            onToggleHandler={(e) =>
              onSubmitFormHandler("submit_request", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
        <div className={style.tab_manage}>
          <FormToggle
            labelHeading={t("transaction_history")}
            checkedToggle={state.transaction_history}
            onToggleHandler={(e) =>
              onSubmitFormHandler("transaction_history", e.target.checked)
            }
            customContainer={{
              flex: "1 1 33%",
            }}
          />
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
          }}
        />
      </div>
      <Notfication
        run={run}
        setRun={setRun}
        msg={msg}
        postiton="bottom"
        type="success"
      />
    </>
  );
};

export default FinanceTabController;
